import React, { useEffect, useState } from 'react'
import Toastify from 'toastify-js'
import "toastify-js/src/toastify.css"
import logo from '../assets/img/download (2).svg'
import auth from '../assets/img/auth-image-Cn3YdnsP.svg'
import auth1 from '../assets/img/auth-vector.svg'
import { Link, Navigate, useNavigate } from 'react-router-dom'
export default function Register() {
  const [val,setVal]=useState('')
  let count=1
  const emailPattern = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$/;
  const numberPattern = /^\d+$/;
  const navigate=useNavigate()
  const show=()=>{
      if(count%2){
        document.getElementById('heigth').style.height='40px';
        document.getElementById('heigth').style.animation='ease-in-out';
        document.getElementById('heigth').style.transition='0.4s';
        document.getElementById('i1').style.transform='rotate(180deg)';
      }else{
        document.getElementById('heigth').style.height='0px';
        document.getElementById('heigth').style.animation='ease-in-out';
        document.getElementById('heigth').style.transition='0.4s';
        document.getElementById('i1').style.transform='rotate(0deg)';
      }

      count++;
  }
  const getData=()=>{
    if(emailPattern.test(val) || numberPattern.test(val) && val!=null){
      const newTask = {
        data:val
      };
      
      fetch('https://66caec954290b1c4f1990ef1.mockapi.io/register', {
        method: 'POST',
        headers: {'content-type':'application/json'},
        // Send your data in the request body as JSON
        body: JSON.stringify(newTask)
      }).then(res => {
        if (res.ok) {
            return res.json();
        }
        // handle error
      }).then(task => {
        Toastify({
          text: "     ",
          duration: 3000,
          backgroundColor:"#F7C213",
          }).showToast();
        navigate('/')
      }).catch(error => {
        Toastify({
          text:error.message,
          duration: 3000,
          backgroundColor:"#f12f2f",
          }).showToast();
      })
    }else{
      Toastify({
        text:"  ",
        duration: 3000,
        backgroundColor:"#f12f2f",
        }).showToast();
    }
  
  }

  return (
   <>
        <section className='w-full  h-full flex justify-center items-center'>
              <div className='w-full *:w-full  h-full lg:h-[100vh] *:h-full flex justify-center items-center'>
                      <div className=' *:w-full flex flex-wrap justify-end  items-start lg:w-1/3 '>
                                <div className='h-full  lg:h-[70%]  *:md:px-5 *:px-2 *:w-full gap-2  *:h-1/4 flex flex-wrap justify-center items-center'>
                                      <div className='py-5 mt-5'>
                                          <figure className='flex justify-center items-center w-full h-full'>
                                            <img src={logo} className='object-contain' />  
                                          </figure>  
                                      </div>          
                                      <div className='flex justify-center items-center *:w-full flex-wrap *:py-2'>
                                          <div className=''>
                                              <h2 className='font-medium  font-yekanbold text-xl'> </h2>
                                          </div>
                                          <div className=''>
                                              <span className=' '>     <b className='px-1'>https://ramzinex.com</b></span>
                                          </div>
                                      </div>          
                                      <div className='flex justify-center items-center *:w-full flex-wrap *:py-2 *:h-1/2'>
                                            <div className='*:my-4 mb-1'>
                                              <label>  / </label>
                                              {/* pattern={emailPattern.source} */}
                                              <input  onChange={(e)=>setVal(e.target.value)}  id='inpVal' type="text" className=" focus:outline-[#EAB111] cursor-pointer h-10 p-2 rounded outline-[#F6F6F6] border w-full md:w-[90%]" placeholder='   ' required></input>
                                            </div>
                                            <div className='*:md:my-5 md:py-5'>
                                              <label onClick={show} className='flex items-center cursor-pointer px-1'>    ()<i id='i1' className="ri-arrow-drop-down-line font-medium px-2"></i></label>
                                              <div id='heigth' className='overflow-hidden h-0'>
                                                  <input  type="text" className="h-full focus:outline-[#EAB111] p-2 rounded outline-[#F6F6F6] border w-full md:w-[90%]" placeholder='   ' ></input>
                                              </div>
                                            </div>
                                      </div>  
                                      <div className='flex mt-8 flex-wrap justify-start items-center *:w-full *:h-10 py-5 *:py-2'>
                                            <button onClick={getData} id='btnReg' className='bg-[#EAB111] rounded flex justify-center items-center md:w-[90%]  cursor-pointer hover:brightness-95'> </button>
                                            <span>   <span className='font-medium text-[#C37D84]'>   </span>   </span>
                                            <span>  <Link to='/login'><span className='font-medium text-[#C37D84]'> </span></Link></span>
                                      </div>        
                                </div>
                      </div>
                      <div className='hidden lg:flex lg:w-2/3 justify-center items-center relative '>
                             <div className='h-full w-1/3 flex justify-end '>
                                    <img src={auth} className='object-contain h-[70%] w-full object-center absolute top-16 left-[10%]' />
                              </div> 
                              <div className='h-full  w-2/3 bg-bg3 bg-cover bg-left-bottom  bg-no-repeat	'>
                                
                              </div>
                              
                      </div>
                      
              </div>
        </section>
   </>
  )
}
