import React, { useEffect, useState } from 'react'
import Toastify from 'toastify-js'
import "toastify-js/src/toastify.css"
import logo from '../assets/img/download (2).svg'
import auth from '../assets/img/auth-image-Cn3YdnsP.svg'
import auth1 from '../assets/img/auth-vector.svg'
import { Link, Navigate, useNavigate } from 'react-router-dom'
export default function Login() {
  const [val,setVal]=useState('')
  const navigate=useNavigate()
  
  const sendData=()=>{
    if(val!=null){
      const url=new URL("https://66caec954290b1c4f1990ef1.mockapi.io/register")
      url.searchParams.append('data',val)
      fetch(url, {
        method: 'GET',
        headers: {'content-type':'application/json'},
        // Send your data in the request body as JSON
       
      }).then(res => {
        if (res.ok) {
            return res.json();
        }
        // handle error
      }).then(task => {
        Toastify({
          text: "     ",
          duration: 3000,
          backgroundColor:"#F7C213",
          }).showToast();
        navigate('/')
      }).catch(error => {
        Toastify({
          text:error.message,
          duration: 3000,
          backgroundColor:"#f12f2f",
          }).showToast();
      })
    }else{
      Toastify({
        text:"  ",
        duration: 3000,
        backgroundColor:"#f12f2f",
        }).showToast();
    }
  
  }

  return (
   <>
        <section className='w-full  h-full flex justify-center items-center'>
              <div className='w-full *:w-full  h-full lg:h-[100vh] *:h-full flex justify-center items-center'>
                      <div className=' *:w-full flex flex-wrap justify-end  items-start lg:w-1/3 '>
                                <div className='h-full  lg:h-[70%]  *:md:px-5 *:px-2 *:w-full gap-2   flex flex-wrap justify-center items-center'>
                                      <div className=' mt-5'>
                                          <figure className='flex justify-center items-center w-full h-full'>
                                            <img src={logo} className='object-contain' />  
                                          </figure>  
                                      </div>          
                                      <div className='flex   justify-center items-center *:py-4 *:w-full flex-wrap '>
                                          <div className='h-10 flex items-center'>
                                              <h2 className='font-medium  font-yekanbold text-xl'> </h2>
                                          </div>
                                          <div className='h-full '>
                                              <span className='h-10 '>     <b className='px-1'>https://ramzinex.com</b></span>
                                          </div>
                                          <div className='*:my-5 mb-1'>
                                              <label>  / </label>
                                              {/* pattern={emailPattern.source} */}
                                              <input  onChange={(e)=>setVal(e.target.value)}  id='inpVal' type="text" className=" focus:outline-[#EAB111] cursor-pointer h-10 p-2 rounded outline-[#F6F6F6] border w-full md:w-[90%]" placeholder='   ' required></input>
                                            </div>
                                            <div className='flex  flex-wrap justify-start items-center *:w-full *:h-10 py-5 *:py-2'>
                                            <button onClick={sendData} id='btnReg' className='bg-[#EAB111] rounded flex justify-center items-center md:w-[90%]  cursor-pointer hover:brightness-95'> </button>
                                            <span className='font-medium text-[#C37D84]'>     </span>   
                                            <span>  <Link to='/register'><span className='font-medium text-[#C37D84]'>  </span></Link></span>
                                      </div>    
                                      </div>          
                                      {/* <div className='flex justify-center items-start *:w-full flex-wrap *:py-2 *:h-[100px]'>
                                            <div className='*:my-2 mb-1'>
                                              <label>  / </label>
                                              {/* pattern={emailPattern.source} 
                                              <input  onChange={(e)=>setVal(e.target.value)}  id='inpVal' type="text" className=" focus:outline-[#EAB111] cursor-pointer h-10 p-2 rounded outline-[#F6F6F6] border w-full md:w-[90%]" placeholder='   ' required></input>
                                            </div>
                                            
                                      </div>   */}
                                      {/* <div className='flex  flex-wrap justify-start items-center *:w-full *:h-10 py-5 *:py-2'>
                                            <button onClick={sendData} id='btnReg' className='bg-[#EAB111] rounded flex justify-center items-center md:w-[90%]  cursor-pointer hover:brightness-95'> </button>
                                            <span>   <span className='font-medium text-[#C37D84]'>   </span>   </span>
                                            <span>  <Link to='/register'><span className='font-medium text-[#C37D84]'>  </span></Link></span>
                                      </div>         */}
                                </div>
                                <div className='w-full'></div>
                                <div className='w-full'></div>
                      </div>
                      <div className='hidden lg:flex lg:w-2/3 justify-center items-center relative '>
                             <div className='h-full w-1/3 flex justify-end '>
                                    <img src={auth} className='object-contain h-[70%] w-full object-center absolute top-16 left-[10%]' />
                              </div> 
                              <div className='h-full  w-2/3 bg-bg3 bg-cover bg-left-bottom  bg-no-repeat	'>
                                
                              </div>
                              
                      </div>
                      
              </div>
        </section>
   </>
  )
}
