const getData=()=>{
     let url="https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=50&page=1&sparkline=false";
     fetch(url)
     .then(res=>res.json())
     .then(data=>{
          let sort=data.sort((c,b)=>c.high_24h-b.price_change_24h)
          let sortvolume=data.sort((c,b)=>c.total_volume-b.fully_diluted_valuation)
          setVal(sort)
          
         
     })
}

