     useEffect(() => {
       const observer = new IntersectionObserver((entries) => {
         const [entry] = entries;
         setIsInView(entry.isIntersecting);
       }, [targetRef, isInView]);
   
       if (targetRef.current) {
         observer.observe(targetRef.current);
       }
   
       return () => {
         if (targetRef.current) {
           observer.unobserve(targetRef.current);
         }
       };
     }, [targetRef, isInView]);

