import React, { useEffect, useState ,useCallback} from 'react'
import _ from 'lodash'
import { useIdStore } from '../store'
import Header from '../components/Header'
import irr from '../assets/img/irr.png'
import { LineChart, lineElementClasses } from '@mui/x-charts/LineChart';
import Chart from "chart.js/auto";
import { Line } from "react-chartjs-2";
import { data } from 'autoprefixer'
import Footer from '../components/Footer'
export default function Currency() {
  const [isButtonActive1, setIsButtonActive1] = useState(true);
  const [isButtonActive2, setIsButtonActive2] = useState(true);
  const store=useIdStore(state=>state.id);
  const [val1,setVal1]=useState([]);
  let sell=document.getElementById('sell');
  let buy=document.getElementById('buy');
  let btn=document.getElementById('btn');
  let cloneArr=[];
  let priceArr=[];
 let lineChart;
   
  
const get=()=>{
  fetch("https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=50&page=1&sparkline=false")
  .then(res=>res.json())
  .then(data=>{
       setVal1(data)
       data.find((v,i)=>{
          if(store==v.id){
            cloneArr.push(v.current_price,i)
          }
       })
      //  alert(cloneArr);
       cloneArr.map((v,i)=>{
          //  alert(v)
          priceArr.push(v)
       })
       lineChart = cloneArr ? (
        <Line
          data={{
            labels: cloneArr.map((data) =>
               data.id
            ),
            datasets: [
              {
                data: cloneArr.map((data) => data.high_24h),
                label: "high",
                borderColor: "rgb(0, 217, 255)",
                fill: true,
              },
              {
                data: val1.map((data) => data.low_24h),
                label: "low",
                borderColor: "red",
                backgroundColor: "rgb(255, 0, 0)",
                fill: true,
              },
            ],
          }}
        />
      ) : null;
     

  })
}
useEffect(()=> {
  get()
}, [])


  return (
   <>
  <section className='w-full h-full overflow-hidden font-yekan'>
      <Header/>
      <div className='w-full h-full flex items-center justify-center absolute top-14 right-0'>
        <div className='w-[80%]  h-full flex  items-start justify-center'>
            <ul  className='w-full  h-full md:h-[500px] flex items-start justify-center flex-wrap *:w-full'> 
                    
          {
             
                
             val1 && val1.map((item,index)=>{ 
                 if(store== item.id){ 
                      return  <li key={item.id} className='w-full   mt-5 h-[600px] md:h-full flex items-center justify-center flex-wrap *:w-full'>
                                   <div className='md:w-2/3 overflow-hidden flex gap-4 flex-wrap justify-start items-start h-full  *:h- px-4'>
                                           <div className='w-full h-[100px] md:h-1/5 flex items-center justify-start mt-4 py-4 *:px-4'>
                                          <figure className='  h-full py-5 flex  justify-start items-center'>
                                                <img src={item.image} className='h-20 w-20  object-contain' />
  
                                          </figure>
                                          <div className='*:py-1'>
                                            <h2 className='text-lg font-bold'>{item.name}</h2>
                                            <h2 className='text-lg font-bold'>{item.symbol}</h2>
                                            </div>
                                          </div>
                                          <div className='w-full h-[100px]  md:h-1/5  font-semibold text-xl'>
                                                  <h3 className='font-yekanbold '>       +    ({item.name})</h3>
                                          </div>
                                          <div className='w-full  h-[300px]  md:h-2/5 *:md:h-full  *:h-1/3 py-4 flex justify-center flex-wrap md:flex-nowrap items-center *:w-full *:md:w-1/3 '>
                                                <div className='flex justify-between items-center md:flex-wrap  *:md:w-full *:md:h-10 md:justify-center'>
                                                  <h2  className='text-[#b8bec3] text-xl '>   </h2>
                                                  <h2 className='text-xl'>{item.current_price}$</h2>
                                                  <h2 className='text-xl hidden md:flex'></h2>
                                                 
                                                </div>
                                                <div className='flex justify-between items-center md:flex-wrap *:md:w-full *:md:h-10 md:justify-center'>
                                                  <h2  className='text-[#b8bec3] text-xl '>   </h2>
                                                  <h2 className='text-xl'>{item.current_price}$</h2>
                                                  <h2 className='text-xl hidden md:flex'></h2>
                                                 
                                                </div>
                                                <div className='flex justify-between *:w-full  items-center md:flex-wrap *:md:w-full *:md:h-10 md:justify-center'>
                                                  <h2  className='text-[#b8bec3] text-xl flex  '>   </h2>
                                                  <h2 className='text-xl justify-end md:justify-start flex text-blue-400 '>{item.price_change_percentage_24h}%</h2>
                                                  <h2 className='text-xl hidden md:flex'></h2>
                                                 
                                                </div>
                                                
                                          </div>
                                          <div className='w-full hidden md:flex  h-10 mb-2  gap-4 *:rounded md:h-1/5   justify-center md:justify-start items-start '>
                                              <button className='bg-[#E29F02] h-9 w-[100px] p-1 flex items-center justify-center hover:brightness-95'>  </button>
                                              <button className='text-[#C17A01] h-9 w-[150px] border bg-white hover:brightness-95'>    </button>
                                          </div>
                                  </div>
                                  <div className='md:w-1/3 mt-5 md:mt-0 border h-full rounded *:h-1/4 '>
                                        <div className='flex justify-center gap-4 items-center font-medium  *:w-[40%] *:h-10'>
                                              <button  id='sell' className='border text-white bg-[#22BD86]'></button>
                                              <button  id='buy' className='border'></button>
                                        </div>
                                        <div className='flex justify-center items-center flex-wrap'>
                                            <h2 className='font-yekanbold  w-[80%] flex justify-start'> </h2>
                                            <div className='border h-10 w-[80%] flex items-center'>
                                                  <img src={irr} className='object-contain h-10 w-10 p-2' />
                                                  <input className='w-full h-full'/>
                                            </div>
                                        </div>
                                        <div className='flex justify-center items-center flex-wrap'>
                                            <h2 className='font-yekanbold  w-[80%] flex justify-start'> </h2>
                                            <div className='border h-10 w-[80%] flex items-center'>
                                                  <img src={item.image} className='object-contain h-10 w-10 p-2' />
                                                  <span>{item.id}</span>
                                                  <input className='w-full h-full'/>
                                            </div>
                                        </div>
                                       
                                        <div className='flex justify-center items-center'>
                                            <button  id='btn' className='w-[80%] h-10 rounded border bg-[#22BD86] text-white'> </button>
                                        </div>
                                  </div>
                                  {/*  */}
                                  <section className='w-full h-full flex justify-center items-start flex-wrap *:w-full px-4 mt-10'>
                                          <div className='h-10 flex justify-start '>
                                                <h2 className='text-xl font-yekanbold font-medium'>  </h2>
                                          </div>
                                          {/*  */}
                                          <div className='flex h-full justify-center items-center'>
                                                    <div className='w-full h-[400px] flex justify-center items-center'>{lineChart}</div>           
                                          </div>
                                  </section>
                      </li>
                  }
                 
             })
           }
              </ul>
          </div>
      </div>
           
  </section>
 
     
   </>
  )
}
